import React from 'react'

function Footer() {
  return <>
  <div className='p-5 mt-3' style={{backgroundColor:"#F8F9FA"}}>
  <div className='container'>
    <h5 style={{fontWeight:"bold",fontSize:"30px"}}>Get The FreshCart App</h5>
    <p>We Well Send You a Link</p>
    <div className='d-flex align-items-center'>
      <input type="email" placeholder='Email...' style={{width:"80%", marginRight:"20px", padding:"5px", fontWeight:"bold", borderRadius:"5px"}} />
      <button className='btn btn-outline-success'>Share App Link</button>
    </div>
    <div style={{borderBottom:"1px solid black"}}>
    <ul className='list-unstyled d-flex justify-content-center m-3 '>
            <li style={{marginRight:"10px", fontSize:"30px", color:"#1877f2"}}>
            <i className="me-2 fa-brands fa-facebook"></i>
            </li>
            <li style={{marginRight:"10px", fontSize:"30px", color:"#e1306c"}}>
            <i class="me-2 fa-brands fa-instagram"></i>
            </li>
            <li style={{marginRight:"10px", fontSize:"30px", color:"#0a66c2"}}>
            <i class="me-2 fa-brands fa-linkedin-in"></i>
            </li>
            <li style={{fontSize:"30px"}}>
            <i style={{color:"#1da1f2"}} class="me-2 fa-brands fa-twitter"></i>
            </li>
          </ul>
    </div>
    <div className="text-center m-3" style={{fontWeight:"bold"}}>
      &copy; 2024 All Rights Reserved
    </div>
    </div>
    </div>
  </>
}

export default Footer
