import React, { useContext } from 'react'
import logo from '../../images/freshcart-logo.svg'
import { Link, useNavigate } from 'react-router-dom'
import { authContext } from '../../Context store/AuthContext'
import { CartContext } from '../../Context store/CartContext';



export default function Navbar() {

const {Token, setToken} = useContext(authContext);
const {numOfCartItems}= useContext(CartContext);
const navigate = useNavigate();

function logout (){

  setToken(null);
  localStorage.removeItem("tkn");
  navigate("/login")
}

  return <>
  <nav className="navbar navbar-expand-lg bg-body-tertiary">
  <div className="container-fluid">
    <Link className="navbar-brand" to="/home">
      <img src={logo} alt="fresh cart" />
    </Link>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      
      
      {Token ? <ul className="navbar-nav me-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <Link className="nav-link active" aria-current="page" to="/home">Home</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to="/categories">categories</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to="/Brand">brands</Link>
        </li>
        <li className="nav-item position-relative">
          <Link className="nav-link" to="/cart">cart</Link>
          <span class="position-absolute top-0 start-100 translate-middle badge rounded-pill bg-danger">
    {numOfCartItems ? numOfCartItems: ""} 
  </span>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to="/AllOrders">All Orders</Link>
        </li>
      </ul>:""}
      
      
      <ul className="navbar-nav ms-auto mb-2 mb-lg-0 align-items-center">
        <li className="nav-item">
          {/* <ul className='list-unstyled d-flex '>
            <li>
            <i className="me-2 fa-brands fa-facebook"></i>
            </li>
            <li>
            <i class="me-2 fa-brands fa-instagram"></i>
            </li>
            <li>
            <i class="me-2 fa-brands fa-linkedin-in"></i>
            </li>
            <li>
            <i class="me-2 fa-brands fa-twitter"></i>
            </li>
          </ul> */}
        </li>

        {Token ?<><li className="nav-item">
         <Link to="/Profile" className="nav-link">Profile </Link> 
        </li>
        <li className="nav-item">
        <span onClick={logout} role='button' className="nav-link">  logout </span>
      </li></> 
        :<>
        <li className="nav-item">
          <Link className="nav-link" to="/login">login</Link>
        </li>

        <li className="nav-item">
          <Link className="nav-link" to="/register">register</Link>
        </li>
        </>}

        


        
      </ul>
    </div>
  </div>
</nav>
  </>
}

  
